body{
    background-color: #000;
}

.navbar-wrapper{
    padding: 1rem 5%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar-logo{
    text-decoration: none;
    color: #fff;
}

.navigation{
    display: flex;
    list-style: none;
    gap: 40px;
    align-items: center;
}

.navigation li a{
    text-decoration: none;
    color: #312d30;
    position: relative;
    transition: .5s;
}

.navigation li a:hover{
    color: #fff;
}

.navigation li a:hover::before{
    content: '';
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: -10px;
    transition: 0.5s;
    border-radius: 4px;
}

.navigation button{
    background-color: transparent;
    color: #4a014a;
    font-weight: bold;
    font-size: 1rem;
    border: 1px solid #4a014a;
    padding: 10px 15px;
    border-radius: 30px;
    cursor: pointer;
}