.bannerHeroContainer{
    padding: 2rem 8%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}


.bannerHeroContainer .text-content{
    max-width: 400px;
}

.bannerHeroContainer .text-content h4{
    color: #fff;
    font-size: 2rem;
    font-weight: lighter;
}

.bannerHeroContainer .text-content span{
    color: #4d01f4;
}

.bannerHeroContainer .text-content h2{
    color: #fff;
    font-size: 2.5rem;
    margin-top: -40px;
}

.bannerHeroContainer .text-content p{
    color: #6666;
    margin-top: -20px;
}

.bannerHeroContainer .text-content button{
    background: linear-gradient(to right, #ff0066 0%, #990099 100%);
    padding: 10px 20px;
    border-radius: 10px;
    color: #fff;
}

.bannerHeroContainer .images{
    width: 500px;
    z-index: 100;
}

.bannerHeroContainer .images img{
    width: 100%;
}


@media (max-width:980px) {
    .bannerHeroContainer{
        flex-wrap: wrap-reverse;
        justify-content: center;
    }
}